import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams, AlertController, MenuController, LoadingController, ModalController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

 public orderitems: any[];
  myPic : any = './assets/img/blankbanner.jpg';

 public total : any = 0;
 public itemcount : any = 0;
 public orderTotal : any=0;
 public freightAmount : any=0;
 public freightValid: boolean = false;
 public userPostcode : any = '';
 public mytext : any = '';
  constructor(public nav: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public modalCtrl: ModalController, public menuCtrl: MenuController, private connect: Connect, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef) {
    this.orderitems = [];
    this.mytext='';
  }

  recalc()
  { if (window.localStorage.getItem('freightValid')) 
      this.freightValid = true;
    else 
      this.freightValid = false;

    if (window.localStorage.getItem('userPostcode'))
      this.userPostcode = window.localStorage.getItem('userPostcode');
    else
      this.userPostcode='';
    
    this.orderitems = [];
       this.total=0;
    this.itemcount=0;
    var c = window.localStorage.getItem('cart');
    if (!c) {
      c='';
      window.localStorage.setItem('cart',"");
    }
    if (c.length) {
       this.menuCtrl.enable(true,'mainmenu'); 
       var i=c.split('~');
       for (var e=0; e<i.length; e++) {
         var f=i[e].split('^');
         var ths = { id: f[0], qty: f[1], pic: 'http://tella.com.au/pics/'+f[0]+'.jpg', title: f[3], price: f[2], company: f[4], personal: f[5], persdets: f[6]};
         this.orderitems.push(ths);
         this.total += parseFloat(f[1]) * parseFloat(f[2]);
         this.itemcount += parseInt(f[1]);
         this.myPic = 'http://tella.com.au/banners/'+f[4]+'.jpg?v=3';
       }
       this.orderTotal = this.total + this.freightAmount;
    }
  }

  ionViewDidEnter()  {
     window.localStorage.removeItem('freightValid');
     this.recalc();
     let that=this;
     let w = window.localStorage.getItem('userTag');
     if (!w) w='';
  }
 


    signOutAlert() {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
            that.doLogout();
          }
        }
      ]
    });
    alert.present();
  }

  doLogout()
  {
     window.localStorage.removeItem('userTag');
     window.localStorage.removeItem('cart');

  }

  loadText()
  { 
 this.checkCode('*'+this.mytext);
  }

  checkCode(s)
  {
         let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Searching..."
	    });
	    loader.present();
      let w = window.localStorage.getItem('userTag');
      if (!w) w='';
        let url = 'getitems.php?code='+s+'&cart='+window.localStorage.getItem('cart')+'&id='+w;
// console.log(url);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.mytext='';
           that.zone.run(() => {
             
             
           });
         },
         err => {
            loader.dismiss();
            that.mytext='';
            this.connect.logError(err)
         }
        ); 
  }

  removeProduct(id)
  { var c = window.localStorage.getItem('cart');
    var i = c.split('~');
    var nw=[];
    for (var e=0; e<i.length; e++) {
      var a = i[e].split('^');
     if (a[0] != id)
        nw.push(i[e]); 
    }
    window.localStorage.setItem('cart',nw.join('~'));
    window.localStorage.removeItem('freightValid');
    this.recalc();
  }   

  addProduct(id)
  { var c = window.localStorage.getItem('cart');
    var i = c.split('~');
    var nw=[];
    for (var e=0; e<i.length; e++) {
      var a = i[e].split('^');
     if (a[0] != id)
        nw.push(i[e]); 
     else {
        var q=parseInt(a[1])+1;
        a[1]=q.toString();
        nw.push(a.join('^'));
     } 
    }
   window.localStorage.setItem('cart',nw.join('~'));
    window.localStorage.removeItem('freightValid');
    this.recalc();
  }

  subProduct(id)
  { 
    var c = window.localStorage.getItem('cart');
    var i = c.split('~');
    var nw=[];
    for (var e=0; e<i.length; e++) {
      var a = i[e].split('^');
     if (a[0] == id)
     {
        var q=parseInt(a[1])-1;
        if (q>0) a[1]=q.toString();
     } 
     nw.push(a.join('^'));
    }
    window.localStorage.setItem('cart',nw.join('~'));
    window.localStorage.removeItem('freightValid');
    this.recalc();
  }


}



